.sidebar {
    flex: 1;
    height: 100%;
    background-color: rgb(251, 251, 255);
    top: 0;
    left:0;
    overflow-x: hidden;
    position: sticky;
    padding-top: 20px;
    z-index: 1;
    
  }

  .sidebarWrapper {
    padding: 20px;
    color: #555;
  }
  
  .sidebarMenu {
    margin-bottom: 10px;
  }
  
  .sidebarTitle {
    font-size: 13px;
    color: rgb(187, 186, 186);
  }
  
  .sidebarList {
    list-style: none;
    padding: 5px;
  }
  
  .sidebarListItem {
    padding: 20px;
    cursor: pointer;
    display: flex;
    margin: 10px;
    align-items: center;
    border-radius: 10px;
  }
  
  .sidebarListItem.active,
  .sidebarListItem:hover {
    background-color: rgb(240, 240, 255);
  }
  
  .sidebarIcon{
      margin-right: 5px;
      font-size: 20px !important;
  }