body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
}

code {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
    monospace;
}

#root {
  position: absolute;
  width: 100vw;
  height: 100vh;
}

.container,
.small-container{
  max-width: 100vw;
  padding: 0 1rem;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

.small-container h1{
    text-align: center;
    color: #0366ee;
}

.muted-button,
a.muted-button {
  background: transparent;
  border: 1px solid #cdcdcd;
  color: #4e4e4e;
}

.muted-button:hover,
.muted-button:focus,
.muted-button:active,
a.muted-button:hover,
a.muted-button:focus,
a.muted-button:active {
  color: #4e4e4e;
  border: 1px solid #818181;
  background: transparent;
}

.round-button,
a.round-button {
  border-radius: 40px;
}

.striped-table tbody tr:nth-child(odd) {
    background-color: #f8f8f8;
}

.contain-table {
    overflow-x: auto;
}

.text-center {
    text-align: center;
}

.text-left {
    text-align: left;
}
  
.text-right {
    text-align: right;
}

 
 [type='color']:hover,
 [type='date']:hover,
 [type='datetime']:hover,
 [type='datetime-local']:hover,
 [type='email']:hover,
 [type='month']:hover,
 [type='number']:hover,
 [type='password']:hover,
 [type='search']:hover,
 [type='tel']:hover,
 [type='text']:hover,
 [type='url']:hover,
 [type='week']:hover,
 [type='time']:hover,
 select:hover,
 textarea:hover {
   border: 1px solid #c5c5c5;
 }

 button {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
 }
 
input {
  background-color: white;
  color: black;
} 

 
 select {
   color: #404040;
   -webkit-appearance: none;
   -moz-appearance: none;
   background: url(data:image/png;base64,)
     right center no-repeat;
   line-height: 1;
 }
 
 select::-ms-expand {
   display: none;
 }
 
 [type='range'] {
   width: 100%;
 }
 
 
 @media (min-width: 600px) {
