:root {
	--border-color: rgb(128, 128, 128);
	--deepPurple: rgb(114, 82, 160);
	--burgundy: rgb(123, 53, 78);
	--palePink: rgb(245, 233, 237);
	--peach: rgb(182, 84, 118);
	--palePurple: rgb(156, 133, 192);
	--dark3: rgb(84, 98, 66);
	--dark2: rgb(124, 146, 99);
	--dark1: rgb(222, 228, 201);
	--light3: rgb(148, 86, 10);
	--light2: rgb(222, 129, 14);
	--light1: rgb(253, 237, 217);
	--iceBlue: rgb(65, 202, 208);
}
@font-face {
	font-family: "Helvetica 65 Medium";
	font-style: normal;
	font-weight: normal;
	src: local("Helvetica 65 Medium"),
		url("./../../fonts/HelveticaNeue-Medium.woff") format("woff"),
		url("./../../fonts/HelveticaNeue-Medium.otf") format("opentype");
}
@font-face {
	font-family: "Helvetica Neue Light";
	font-style: normal;
	font-weight: normal;
	src: local("Helvetica Neue Light"),
		url("./../../fonts/HelveticaNeueLight.woff") format("woff"),
		url("./../../fonts/HelveticaNeueLight.ttf") format("truetype");
}

body {
	font-family: "Poppins", sans-serif !important;
	background: aliceblue;
}
.wrapper {
	font-size: 10px;
}

.form-vertical {
	position: relative;
}
.form-horizontal {
	position: relative;
	text-align: center;
}
.tbody {
	font-size: 0px;
}

#title {
	margin-left: auto;
	margin-right: auto;
	margin-top: 10px;
	font-family: "Helvetica 65 Medium";
	color: var(--deepPurple);
	font-weight: 800;
	font-size: 35px;
}
#graphTitle {
	color: gray;
	font-size: 20px;
	font-weight: 500;
	margin-top: 5px;
	text-align: center;
	font-family: "Helvetica Neue Light";
}

#csvButton {
	background: var(--iceBlue);
	color: white;
	font-family: "Helvetica Neue Light";
	margin-bottom: 10px;
	width: 20vw;
}

